.shape-layer {
  position: absolute;
  user-select: none;
  outline: 1px solid transparent; }

.shape-layer:focus {
  outline: none; }

.shape-layer.active {
  outline: 1px solid #70c0ff !important;
  z-index: 1; }

.shape-layer .shape-content {
  width: 100%;
  height: 100%;
  overflow: hidden; }

.shape-scale__point {
  position: absolute;
  background: #fff;
  border: 1px solid #59c7f9;
  width: 8px;
  height: 8px;
  z-index: 1;
  border-radius: 50%;
  user-select: none;
  box-sizing: border-box; }

.shape-scale__point[data-point='lt'] {
  margin-left: -4px;
  margin-top: -4px;
  left: 0;
  top: 0;
  cursor: nw-resize; }

.shape-scale__point[data-point='rt'] {
  margin-right: -4px;
  margin-top: -4px;
  right: 0;
  top: 0;
  cursor: ne-resize; }

.shape-scale__point[data-point='lb'] {
  margin-left: -4px;
  margin-bottom: -4px;
  left: 0;
  bottom: 0;
  cursor: sw-resize; }

.shape-scale__point[data-point='rb'] {
  margin-right: -4px;
  margin-bottom: -4px;
  right: 0;
  bottom: 0;
  cursor: se-resize; }

.shape-scale__point[data-point='lm'] {
  margin-left: -4px;
  margin-top: -4px;
  left: 0;
  top: 50%;
  cursor: w-resize; }

.shape-scale__point[data-point='tm'] {
  margin-left: -4px;
  margin-top: -4px;
  left: 50%;
  top: 0;
  cursor: n-resize; }

.shape-scale__point[data-point='rm'] {
  margin-right: -4px;
  margin-top: -4px;
  right: 0;
  top: 50%;
  cursor: e-resize; }

.shape-scale__point[data-point='bm'] {
  margin-left: -4px;
  margin-bottom: -4px;
  left: 50%;
  bottom: 0;
  cursor: s-resize; }
